.sidebar {
  width: 250px;
  height: 100vh;
  background-color: #f5f1ea; /* tono nude/beige */
  position: fixed;
  left: -250px;
  top: 0;
  transition: left 0.3s ease;
  padding: 20px;
  box-shadow: 2px 0 5px rgba(0,0,0,0.1);
}

.sidebar.open {
  left: 0;
}

.hamburger-btn {
  background-color: #d9cfc3;
  border: none;
  font-size: 1.5rem;
  color: #4a3f35;
  cursor: pointer;
  padding: 10px 15px;
  border-radius: 5px;
  margin-bottom: 20px;
}

.sidebar a {
  color: #4a3f35;
  text-decoration: none;
  font-weight: bold;
}

.sidebar a:hover {
  text-decoration: underline;
}
